.background {
    overflow: hidden;
    height: 0;
}

.circle {
    position: fixed;
    border-radius: 50%;
    animation: ripple 10s infinite;
    box-shadow: 0px 0px 0.1px 0px;
}

.top {
    top: 10px;
}

.bottom {
    bottom: 10px;
}


.left {
    left: 10px;
}

.right {
    right: 10px;
}


.ripple1 {
    width: 160px;
    height: 160px;
    background:rgba(40, 60, 255, 0.2);
    animation-delay: -6.6s;
}

.ripple2 {
    width: 200px;
    height: 200px;
    background: rgba(40, 60, 255, 0.2);
    animation-delay: -3.3s;
}

.ripple3 {
    width: 240px;
    height: 240px;
    background: rgba(80, 60, 255, 0.2);
}


@keyframes ripple{
    0% {transform: scale(1.5)}
    50% {transform: scale(5.0)}
    100% {transform: scale(1.5)}
}

