
.container {
  background: linear-gradient(-60deg, #cb3d12, #b91a1a, #b7d523, #c96100);
  background-size: 400% 400%;
	animation: gradient 15s ease infinite;
}

.scroller {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding-top: 5vh;
  position: relative;
}

.slot {
  margin-bottom: 20vh;
  justify-content: center;
  width: 80vw;
  height: 80vh;
  z-index: 1;
}


h1 {
  font-size: 3.25vh;
  padding: 20px 0 0 100px;
  transform: skew(-30deg);
}

h2 {
  font-size: 2.5vh;
}

p {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 3vh;
}

@keyframes gradient {
	0% {background-position: 0% 50%}
	50% {background-position: 100% 50%}
	100% {background-position: 0% 50%}
}