
.aboutBanner {
    width: 80vw;
    height: 80px;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: 0px 0px 5px 0px;
    transform: skew(30deg);
    margin-bottom: 5vh;
}

.aboutPanel {
    justify-content: center;
    border: 3px solid rgba(255, 255, 255, 0.1);
    background-color: rgba(255, 255, 255, 0.1);
    margin: auto;
    height: 70vh;
    width: 80vw;
}

.indent {
    text-indent: 30px;
}