
.nav {
    position: absolute;
    top: 35%;
}

.nav ul {
    text-decoration: none;
    list-style: none;
}

.nav ul li {
    color: white;
    margin-bottom: 20px;
    background:rgba(10, 60, 255, 0.5);
    border-width:4px 30px 4px 30px;
    border-style: solid;
    border-color: rgba(102, 127, 173, 0.1) rgba(102, 127, 208, 0.1)  rgba(103, 148, 201, 0.1) rgba(103, 148, 201, 0.1);
    border-radius:0 120px 0 120px;
    -moz-border-radius:0 120px 0 120px;
    -webkit-border-radius:0 120px 0 120px;

}